'use client';

import { useState } from 'react';
import SubmittedApplications from './tabs/SubmittedApplications';
import ApprovedApplications from './tabs/ApprovedApplications';
import Reports from './tabs/Reports';
import AccountOpening from './tabs/AccountOpening';
import SearchBar from '../components/SearchBar'; // ‚úÖ SearchBar present

const tabs = [
  { label: 'Submitted Applications', key: 'submitted' },
  { label: 'Approved Applications', key: 'approved' },
  { label: 'Reports', key: 'reports' },
  { label: 'Account Opening', key: 'account' },
];

export default function DashboardPage() {
  const [activeTab, setActiveTab] = useState('submitted');

  const renderActiveTab = () => {
    switch (activeTab) {
      case 'submitted':
        return <SubmittedApplications />;
      case 'approved':
        return <ApprovedApplications />;
      case 'reports':
        return <Reports />;
      case 'account':
        return <AccountOpening />;
      default:
        return null;
    }
  };

  return (
    <div className="font-sans bg-gray-100 min-h-screen text-gray-900">
      <header className="bg-gradient-to-r from-[#0057A0] to-[#C8102E] text-white py-6 text-center">
        <h1 className="text-2xl font-bold">Prime Pillars ‚Äì Admin Dashboard</h1>
      </header>

      <main className="max-w-5xl mx-auto my-10 bg-white p-8 rounded-lg shadow">
        <SearchBar />

        <div className="flex flex-wrap gap-2 mb-6 justify-center">
          {tabs.map((tab) => (
            <button
              key={tab.key}
              onClick={() => setActiveTab(tab.key)}
              className={`px-4 py-2 text-sm rounded-full transition ${
                activeTab === tab.key
                  ? 'bg-[#C8102E] text-white shadow'
                  : 'bg-[#0057A0] text-white hover:bg-[#C8102E]'
              }`}
            >
              {tab.label}
            </button>
          ))}
        </div>

        <section className="mt-4">{renderActiveTab()}</section>
      </main>
    </div>
  );
}
